import Email from "../assets/email.jpg";
import "../styles/Contact.css";
import { Button, TextField, Dialog, DialogContent, DialogContentText, DialogActions } from '@mui/material'
import React, { useState } from "react";




function Contact() {
  const [dialogOpen, setDialogOpen] = useState(false);
  const [clearForm, setClearForm] = useState('');

  const handleOpenDialog = () => {
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setClearForm('');
  };

  return (
    <div className="contact">
      <div
        className="leftSide"
        style={{ backgroundImage: `url(${Email})` }}
      >
      </div>
      <div className="rightSide">
        <h1>Contact Me</h1>
        <div className="contact-form" >
          <TextField defaultValue={clearForm} label="Full Name" variant="standard" placeholder='Enter full name' />
          <TextField defaultValue={clearForm} label="Email" variant="standard" placeholder='Enter email' />
          <TextField defaultValue={clearForm} label="Message" variant="standard" multiline rows={6} placeholder='Enter message' />
          <Button variant="contained" onClick={handleOpenDialog}>Send Message</Button>
          <Dialog open={dialogOpen} onClose={handleCloseDialog}>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                Message sent. Please refrain from holding breath awaiting reply.
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDialog} autoFocus>
                OKAY
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </div>
    </div>
  );
}

export default Contact;
